import React from "react";
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import Footer from "examples/Footer";
import DataTable from "examples/Tables/DataTable";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { axiosInstanceAuth } from "../../apiInstances/index";
import { toast } from "react-toastify";
import Pagination from "@mui/material/Pagination";
import Stack from "@mui/material/Stack";

import { TextField } from "@mui/material";

const NewsLetter = () => {
  let navigate = useNavigate();
  const [currentPage, setCurrentPage] = useState(1);
  const [page, setPage] = useState(1);
  const [requestData, setRequestData] = useState([]);
  const [oldRequestData, setOldRequestData] = useState([]);

  const [loading, setLoading] = useState(true);

  const checkToken = () => {
    const Token = localStorage.getItem("Token");
    if (!Token) {
      navigate("/sign-in");
    }
  };

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const getUser = () => {
    try {
      axiosInstanceAuth
        .get(`/ecoforest/getnewsletter`)
        .then((res) => {
          const responseData = res.data;
          // console.log(" ~ responseData", responseData.data);

          if (responseData.status) {
            setPage(Math.ceil(res.data.count / 10));

            const temp = [];
            // data.role === "user" &&

            responseData.data.map((data, index) => {
              temp.push({
                id: (
                  <MDTypography component="a" variant="button" color="text" fontWeight="medium">
                    {index + 1}
                  </MDTypography>
                ),
                email: (
                  <MDTypography component="a" variant="button" color="text" fontWeight="medium">
                    {data.email}
                  </MDTypography>
                ),
              });
            });
            setOldRequestData(temp);
            setRequestData(temp);
            setLoading(false);
          }
        })
        .catch(() => {
          toast.error("Something went wrong");
        });
    } catch (error) {
      console.log(" ~ getUser ~ error", error);
    }
  };

  const columns = [
    { Header: "id", accessor: "id", align: "left" },
    { Header: "email", accessor: "email", align: "left" },
  ];
  // nodes
  const handleInput = (e) => {
    if (e.target.value) {
      const searchData = oldRequestData.filter((i) => {
        // console.log(" ~ searchData ~ i", i);
        return (
          i?.email?.props?.children
            ?.toLowerCase()
            ?.trim()
            ?.includes(e.target.value.toLowerCase().trim()) ||
          i?.full_name?.props?.children
            ?.toLowerCase()
            ?.trim()
            ?.includes(e.target.value.toLowerCase().trim()) ||
          i?.Message?.props?.children
            ?.toLowerCase()
            ?.trim()
            ?.includes(e.target.value.toLowerCase().trim())
        );
      });
      setRequestData(searchData);
    } else {
      setRequestData(oldRequestData);
    }
    // getUser({ page_no: currentPage, search: e.target.value });
  };

  const onPaginationTable = (event, page) => {
    setCurrentPage(page);
    getUser({ page_no: page, search: "" });
  };

  useEffect(() => {
    getUser({ page_no: currentPage, search: "" });
  }, []);

  useEffect(() => {
    checkToken();
  }, []);
  return (
    <>
      <DashboardNavbar />
      <DashboardLayout>
        {loading ? (
          <div className="snippet" data-title=".dot-spin">
            <div className="stage">
              <div className="dot-spin"></div>
            </div>
          </div>
        ) : (
          <MDBox pt={6} pb={3}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Card>
                  <MDBox
                    mx={2}
                    mt={-3}
                    py={3}
                    px={2}
                    variant="gradient"
                    bgColor="coinTitleColor"
                    borderRadius="lg"
                    coloredShadow="info"
                  >
                    <div className="box-user">
                      <MDTypography variant="h6" color="white">
                        Newsletter
                      </MDTypography>
                    </div>
                  </MDBox>
                  <MDBox pt={3}>
                    <MDBox pr={1} classNane="inputsre">
                      <TextField type="text" label="Search here" onChange={handleInput} />
                    </MDBox>

                    <DataTable
                      table={{ columns: columns, rows: requestData }}
                      // isSorted={true}
                      entriesPerPage={false}
                      showTotalEntries={false}
                      noEndBorder
                    />

                    {requestData.length > 0 && (
                      <Stack padding={2}>
                        <Pagination onChange={onPaginationTable} count={page} size="large" />
                      </Stack>
                    )}
                  </MDBox>
                </Card>
              </Grid>
            </Grid>
          </MDBox>
        )}
        <Footer />
      </DashboardLayout>
    </>
  );
};

export default NewsLetter;
