import ContactsIcon from "@mui/icons-material/Contacts";
import EmailIcon from "@mui/icons-material/Email";
import User from "layouts/user";
import NewsLetter from "layouts/newsLetter";

const routes = [
  {
    type: "collapse",
    name: "User",
    key: "user",
    icon: <ContactsIcon />,
    route: "/user",
    component: <User />,
  },

  {
    type: "collapse",
    name: "NewsLetter",
    key: "newsletter",
    icon: <EmailIcon />,
    route: "/newsletter",
    component: <NewsLetter />,
  },
  {
    //   type: "collapse",
    //   name: "Sign In",
    //   key: "sign-in",
    //   icon: <Icon fontSize="small">login</Icon>,
    route: "/sign-in",
    component: <SignIn />,
  },
  {
    // type: "collapse",
    //name: "Sign Up",
    // key: "sign-up",
    // icon: <Icon fontSize="small">assignment</Icon>,
    route: "/sign-up",
    component: <SignUp />,
  },
];

export default routes;
